.video-container{
    min-height: 100vh;
    background-image: radial-gradient(#fff,#00B4D8);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

video{
    height:80vh;
    width:80vw;
}

@media screen and (max-width:1000px) {
    video{
        width:98vw;
        height: auto;
    }
}